import { React, useEffect, useState } from "react";
import Project from "./Project";
import ProjectCategories from "./ProjectCategories";
import { designData }  from "../data/Design";
import { codeData } from "../data/Code";
import { completeData } from "../data/Complete";

export default function Projects() {
  const [selected, setSelected] = useState("complete");
  const [data, setData] = useState([]);
  const list = [
    {
      id: "complete",
      title: "everything",
    },
    {
      id: "code",
      title: "code",
    },
    {
      id: "design",
      title: "design",
    },
  ];

  useEffect(() => {
    switch (selected) {
      case "completed":
        setData(completeData);
        break;
      case "code":
        setData(codeData);
        break;
      case "design":
        setData(designData);
        break;
      default:
        setData(completeData);
    }
  }, [selected]);

  return (
    <div class="projects flex-grow-1">
      <div class="container-fluid">
        <div class="row">
          <div class="col-1"></div>
          <div class="col-10 d-flex flex-column align-items-center">
            <h1 class="text-center">My projects</h1>
            <ul class="list-group list-group-horizontal"> 
              {list.map((item) => (
                <ProjectCategories
                  title={item.title}
                  active={selected === item.id}
                  setSelected={setSelected}
                  id={item.id}
                />
              ))}
            </ul>
            <div class="projects-container">
              <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3">
                {data.map((project) => {
                  return (
                    <div class="my-2">
                      <Project
                      title = {project.title}
                      image = {project.image}
                      tags = {project.tags}
                      />
                    </div>
                  )
                })}
              </div>
            </div>
          </div>
          <div class="col-1"></div>
        </div>
      </div>
    </div>
  );
}